  background-size: 1.2em;
  padding-left: 1.75em;
  margin-bottom: 0.8em;
}
.text-content ul li:not(:first-child), .footer-1__text ul li:not(:first-child) {
  margin-bottom: 1.5rem;
}
.text-content ul li::marker, .footer-1__text ul li::marker {
  margin-top: 10px;
}

@media (max-width: 767.98px) {
  .wide-grid__item:nth-child(n+2) {
    margin-top: 1.5rem;
  }
}
@media (min-width: 768px) and (max-width: 991.98px) {
  .wide-grid__item:nth-child(n+3) {
    margin-top: 1.5rem;
  }
}
@media (min-width: 992px) {
  .wide-grid__item:nth-child(n+4) {
    margin-top: 1.5rem;
  }
}

